.header_select{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  }

.header_select_title{
  position: relative;
  width: 83px;
  color: #5d5d5d;
  font-size: 10px;
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 1.25px;

  &::after{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    background-image: url("./../../img/icons/icons8-стрелка-вниз-в-круге-64.png"); /*вставить стрелку*/
  }

}

.header_select_dropdown{
  position: absolute;
  top: 100%;
  width: 144px;
  border: 2px solid #303030;
  border-radius: 0 0 4px 4px;

}

